function tightenList(tokens) {
    var iteratedElements = 0;
    var insideListItem = false;
    var isTight = false;
    for (var i = 0; i < tokens.length; i++) {
        var token = tokens[i];
        if (token.type === "bullet_list_open" ||
            token.type === "ordered_list_open") {
            var result = tightenList(tokens.slice(i + 1));
            if (result.isTight) {
                token.attrSet("tight", result.isTight.toString());
            }
            // all the content from the current index to the close tag is taken care of
            // skip to the next node
            i += result.iteratedElements + 1;
            continue;
        }
        else if (token.type.startsWith("list_item_")) {
            insideListItem = token.type.endsWith("open");
        }
        else if (token.type.startsWith("paragraph_") &&
            insideListItem &&
            token.hidden) {
            isTight = true;
        }
        else if (token.type === "bullet_list_close" ||
            token.type === "ordered_list_close") {
            // we found the end of this list, stop here
            iteratedElements = i;
            break;
        }
    }
    return {
        iteratedElements: iteratedElements,
        isTight: isTight,
    };
}
/**
 * Searches for and marks tight lists with a "tight" attribute
 */
export function tight_list(md) {
    md.core.ruler.push("tight-list", function (state) {
        tightenList(state.tokens);
        return false;
    });
}
